import { Saves } from "./saves.model";
import { Skills } from "./skills.model";
import { Stats } from "./stats.model";


export class Personaje {
    nombre: string;
    id: number;
    level: number;
    clase: string;
    background: number;
    raza: number;
    alineamiento: string;
    stats: Stats;
    proficiencia: number;
    hp: number;
    skills: Skills;
    savings: Saves;
    listaStats: number[];

    constructor(nombre: string) {
        this.id = new Date().getTime();
        this.nombre = nombre;
    }


}